import React, { useState, useEffect, useRef } from 'react';
import './App.css';
import $ from 'jquery';
import { addAnimationByIds, genCss } from './Anim';
import { useInterval } from './Tools';
import MemorySquareModal from './modal/MemorySquareModal';
import NewPlayModal from './modal/NewPlayModal';
import GameOverModal from './modal/GameOverModal';
require('bootstrap');


function Header(props) {
  const remainNums = props.remainNums;
  const remainSeconds = props.remainSeconds;
  const combos = props.combos;
  const score = props.score;

  return (
    <div style={{ "height": "10vh" }} class="bg-light">
      <div class="container-fluid h-100 m-0">
        <div class="row align-items-center" style={{ "height": (10 / 2) + "vh" }}>
          <div class="col-3">
            Num
          </div>
          <div class="col-3">
            Time
          </div>
          <div class="col-3">
            Combos
          </div>
          <div class="col-3">
            Score
          </div>
        </div>
        <div class="row text-info align-items-center" style={{ "height": (10 / 2) + "vh" }}>
          <div class="col-3">
            {remainNums}
          </div>
          <div class="col-3">
            {remainSeconds}
          </div>
          <div class="col-3">
            {combos}
          </div>
          <div class="col-3">
            {score}
          </div>
        </div>
      </div>
    </div>
  )
}

function Footer(props) {
  return (
    <div style={{ "height": "8vh" }} class="bg-light">
      <div class="container-fluid h-100">
        <div class="row h-100 pt-2">
          <div class="col">
            <button type="button" class="btn btn-outline-info btn-sm w-100" onClick={props.onNewPlayClick}>
              New Play
            </button>
          </div>
          <div class="col">
            <button type="button" class="btn btn-outline-info btn-sm w-100" onClick={props.onWhatsThisClick}>
              What's this
            </button>
          </div>
        </div>
      </div>

    </div>
  )
}

function SquareArea(props) {
  const gameConfig = {
    rows: props.rows,
    cols: props.cols,
  }

  function onSquareClick(x, y) {
    props.onSquareClick(x, y);
  }

  let gameRows = [];
  for (let i = 0; i < gameConfig["rows"]; i++) {
    let gameCols = [];
    for (let j = 0; j < gameConfig["cols"]; j++) {
      let col =
        <div class="col p-1 h-100" key={`gameCols-${j}`}>
          <Square x={i} y={j} key={`gameCols-${i}-${j}`} onSquareClick={onSquareClick} />
        </div>;
      gameCols.push(col);
    }
    gameRows.push(
      <div key={`gameRows-${i}`} class="row" style={{ "height": (82 / gameConfig["rows"]) + "vh" }}>
        {gameCols}
      </div>
    )
  }

  return (
    <div class="container-fluid">
      {gameRows}
    </div>
  )
}

function Square(props) {
  const x = props.x;
  const y = props.y;

  function onSquareClick() {
    props.onSquareClick(x, y);
  }

  return (
    <button type="button" class="btn btn-light w-100 h-100" style={{ "borderRadius": "0" }}
      onClick={onSquareClick} id={`square-${x}-${y}`}></button>
  )
}

function App() {
  const memorySquareModalId = "memorySquareModalId";
  const newPlayModalId = "newPlayModalId";
  const gameOverModalId = "gameOverModalId";

  const [gapSeconds, setGapSeconds] = useState(0);
  const [remainSeconds, setRemainSeconds] = useState(0);
  const [remainSquares, setRemainSquares] = useState([]);
  const [totalSquaresLength, setTotalSquaresLength] = useState(3);
  const [level, setLevel] = useState(1);
  const [rows, setRows] = useState(3);
  const [cols, setCols] = useState(3);
  const [correctIncreaseSeconds, setCorrectIncreaseSeconds] = useState(1);
  const [wrongDecreaseSeconds, setWrongDecreaseSeconds] = useState(1);
  const [questionCss, setQuestionCss] = useState(genCss("#66ccff", "inherit", 0.6));
  const [correctCss, setCorrectCss] = useState(genCss("green", "inherit", 0.6));
  const [wrongCss, setWrongCss] = useState(genCss("red", "inherit", 0.6));
  const [combos, setCombos] = useState(0);
  const [score, setScore] = useState(0);
  const [isStart, setIsStart] = useState(false);
  const [firstIn, setFirstIn] = useState(true);

  const [difficulty, setDifficulty] = useState("Easy");
  const allDifficulty = ["Easy", "Medium", "Hard"];

  function genSquares(totalSquares, rows, cols) {
    let newSquares = [];
    while (newSquares.length < totalSquares) {
      let newSquare = [];
      newSquare.push(Math.floor(Math.random() * rows));
      newSquare.push(Math.floor(Math.random() * cols));
      newSquares.push(newSquare);
    }
    return newSquares;
  }

  function genSqByLevel(level) {
    let newSquares;
    if (level === 5) {
      console.log(level);
      newSquares = genSquares(
        totalSquaresLength + 1, rows + 1, cols + 1
      )
      setTotalSquaresLength(totalSquaresLength + 1);
      setRows(rows + 1);
      setCols(cols + 1);
    } else if (level === 10) {
      newSquares = genSquares(
        totalSquaresLength + 1, rows, cols
      );
      setTotalSquaresLength(totalSquaresLength + 1);
    } else if (level === 15) {
      newSquares = genSquares(
        totalSquaresLength + 1, rows + 1, cols + 1
      )
      setTotalSquaresLength(totalSquaresLength + 1);
      setRows(rows + 1);
      setCols(cols + 1);
    } else if (level === 20) {
      newSquares = genSquares(
        totalSquaresLength + 1, rows + 1, cols + 1
      )
      setTotalSquaresLength(totalSquaresLength + 1);
      setRows(rows + 1);
      setCols(cols + 1);
    } else if (level % 5 === 0) {
      newSquares = genSquares(
        totalSquaresLength + 1, rows, cols
      )
      setTotalSquaresLength(totalSquaresLength + 1);
    } else {
      newSquares = genSquares(
        totalSquaresLength, rows, cols
      );
    }
    return newSquares;
  }

  function nextGame(ifNew = true) {
    let newSquares;
    let questionCss;
    if (ifNew) {
      let gapSeconds;
      switch (difficulty) {
        case "Easy":
          gapSeconds = 0.6;
          break;
        case "Medium":
          gapSeconds = 0.4;
          break;
        case "Hard":
          gapSeconds = 0.3;
          break;
      }
      setGapSeconds(gapSeconds);
      setLevel(1);
      setRemainSeconds(10);
      setRows(3);
      setCols(3);
      setCorrectIncreaseSeconds(1);
      setWrongDecreaseSeconds(1);
      questionCss = genCss("#66ccff", "inherit", gapSeconds);
      setQuestionCss(questionCss);
      setCorrectCss(genCss("green", "inherit", gapSeconds));
      setWrongCss(genCss("red", "inherit", gapSeconds));
      setCombos(0);
      setScore(0);
      setTotalSquaresLength(3);
      newSquares = genSquares(3, 3, 3);
    } else {
      setLevel(level + 1);
      setRemainSeconds(remainSeconds + 10);
      questionCss = genCss("#66ccff", "inherit", gapSeconds);
      newSquares = genSqByLevel(level + 1);
    }
    setRemainSquares(newSquares);
    const ids = [];
    for (let i = 0; i < newSquares.length; i++) {
      const id = `square-${newSquares[i][0]}-${newSquares[i][1]}`;
      ids.push(id);
    }
    addAnimationByIds(questionCss, ids, true, () => {
      setIsStart(true);
    });
    if (firstIn) {
      setFirstIn(false);
    }
  }

  function gameOver() {
    $(`#${gameOverModalId}`).modal('show');
  }

  function nextOrStop() {
    if (firstIn) {
      return;
    }
    setIsStart(false);
    if (remainSeconds > 0) {
      if (isStart) {
        nextGame(false);
      }
    } else {
      gameOver();
    }
  }

  useEffect(() => {
    if (remainSeconds <= 0 || remainSquares.length <= 0 || !isStart) {
      nextOrStop();
    }
  }, [remainSeconds, remainSquares]);

  useInterval(() => {
    if (remainSeconds > 0 && remainSquares.length > 0 && isStart) {
      setRemainSeconds(remainSeconds - 1);
    }
  }, 1000);


  function onSquareClick(x, y) {
    if (remainSquares.length === 0 || remainSeconds <= 0 || !isStart) {
      return;
    }
    let id = [`square-${x}-${y}`];
    const currentSquares = remainSquares[0];
    if (x === currentSquares[0] && y === currentSquares[1]) {
      setRemainSquares(remainSquares.slice(1));
      setRemainSeconds(remainSeconds + correctIncreaseSeconds);
      addAnimationByIds(correctCss, id, false);
      setCombos(combos + 1);
      setScore(score + 1 * (combos + 1));
    } else {
      setRemainSeconds(remainSeconds - wrongDecreaseSeconds);
      addAnimationByIds(wrongCss, id, false);
      setCombos(0);
    }
  }

  function onNewPlayClick() {
    setIsStart(false);
    $(`#${newPlayModalId}`).modal('show');
  }

  function onWhatsThisClick() {
    setIsStart(false);
    $(`#${memorySquareModalId}`).modal('show');
  }

  function onDiffClick(value) {
    setDifficulty(value);
  }

  function onConfirmClick() {
    nextGame(true);
  }

  return (
    <div className="App">
      <header>
        <Header
          remainSeconds={remainSeconds}
          remainNums={remainSquares.length}
          combos={combos}
          score={score}
        />
      </header>
      <main>
        <SquareArea
          rows={rows}
          cols={cols}
          onSquareClick={onSquareClick}
        />
      </main>
      <footer>
        <Footer
          onNewPlayClick={onNewPlayClick}
          onWhatsThisClick={onWhatsThisClick}
        />
      </footer>

      <MemorySquareModal
        modalId={memorySquareModalId}
      />
      <NewPlayModal
        modalId={newPlayModalId}
        difficulty={difficulty}
        allDifficulty={allDifficulty}
        onDiffClick={onDiffClick}
        onConfirmClick={onConfirmClick}
      />
      <GameOverModal
        modalId={gameOverModalId}
        difficulty={difficulty}
        score={score}
      />
    </div >
  );
}

export default App;
