import $ from 'jquery';

export function addAnimationByIds(animClass, effectIds, ifOrder = true, cb) {
    var animationEvent = 'webkitAnimationEnd oanimationend msAnimationEnd animationend';
    if (ifOrder) {
        $(`#${effectIds[0]}`).addClass(`${animClass}`);
        $(`#${effectIds[0]}`).one(animationEvent, function (event) {
            $(`#${effectIds[0]}`).removeClass(`${animClass}`);
            setTimeout(() => {
                let newEffectIds = effectIds.slice(1);
                if (newEffectIds.length > 0) {
                    addAnimationByIds(animClass, newEffectIds, ifOrder, cb);
                } else {
                    if (cb) {
                        cb();
                    }
                }
            }, 1);

        });
    } else {
        for (let i = 0; i < effectIds.length; i++) {
            $(`#${effectIds[i]}`).addClass(`${animClass}`);
            $(`#${effectIds[i]}`).one(animationEvent, function (event) {
                $(`#${effectIds[i]}`).removeClass(`${animClass}`);
            });
        }

    }
}


function genName(length) {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz";

    for (var i = 0; i < length; i++)
        text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
}

export function genCss(fromColor, toColor, animSeconds) {
    let sheet = document.createElement('style');
    let cssName = `scale-in-center-${genName(10)}`;
    sheet.innerHTML = `
    .${cssName} {
        -webkit-animation: ${cssName} ${animSeconds}s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
        animation: ${cssName} ${animSeconds}s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
      }

    @-webkit-keyframes ${cssName} {
        0% {
            -webkit-transform: scale(0);
            transform: scale(0);
            opacity: 1;
            color: ${fromColor};
        }
        
        
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
            opacity: 1;
            color: ${toColor};
        }
    }
    
    @keyframes ${cssName} {
        0% {
            -webkit-transform: scale(0);
            transform: scale(0);
            opacity: 1;
            background-color: ${fromColor};
        }
        
        
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
            opacity: 1;
            background-color: ${toColor};
        }
    }
    `;
    document.body.appendChild(sheet);
    return cssName;
}