import React from 'react';

function DifficultyItem(props) {
  const value = props.value;
  const active = props.active;

  let final;
  if (active) {
    final = <div class="col p-0 mx-1">
      <button type="button" class="btn btn-primary btn-sm w-100" style={{ "borderRadius": "0" }} onClick={onItemClick}>
        {value}
      </button>
    </div>;
  } else {
    final = <div class="col p-0 mx-1">
      <button type="button" class="btn btn-outline-primary btn-sm w-100" style={{ "borderRadius": "0" }} onClick={onItemClick}>
        {value}
      </button>
    </div>;
  }

  function onItemClick() {
    props.onItemClick(value);
  }

  return (
    <>
      {final}
    </>
  )
}

function NewPlayModal(props) {
  const modalId = props.modalId;
  const difficulty = props.difficulty;
  const allDifficulty = props.allDifficulty;

  function onDiffClick(value) {
    props.onDiffClick(value);
  }

  const btns = allDifficulty.map((value) => {
    if (value === difficulty) {
      return <DifficultyItem
        value={value}
        active={true}
        onItemClick={onDiffClick}
      />;
    } else {
      return <DifficultyItem
        value={value}
        active={false}
        onItemClick={onDiffClick}
      />;
    }

  })

  function onCancelClick() {
  }

  function onConfirmClick() {
    props.onConfirmClick();
  }

  return (
    <div>
      <div class="modal fade" id={modalId} tabIndex="-1" role="dialog" aria-labelledby="editingModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">New Play Config</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="container-fluid">
                <div class="row">
                  {btns}
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal" onClick={onCancelClick}>Cancel</button>
              <button type="button" class="btn btn-primary" data-dismiss="modal" onClick={onConfirmClick}>Confirm</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default NewPlayModal;