import React from 'react';

function GameOverModal(props) {
  const modalId = props.modalId;
  const difficulty = props.difficulty;
  const score = props.score;

  return (
    <div>
      <div class="modal fade" id={modalId} tabIndex="-1" role="dialog" aria-labelledby="editingModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Game Over</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p>Here is your data</p>
              <h5>Difficulty</h5>
              <h1 class="text-primary">{difficulty}</h1>
              <h5>Socre</h5>
              <h1 class="text-warning">{score}</h1>
              <p>Please click <span class="text-info">New Play</span> button to start a new game :)</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">Okay</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default GameOverModal;